@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@500;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.main__container {
  background-color: rgba(255, 255, 255, 0.63);
}
.container {
  margin: 40px auto ;
  width: 650px;
  min-height: 800px;
  border-radius: 5px;
  box-shadow: 0 10px 30px rgba(75,89,123,.13);
  font-family: "Ubuntu", sans-serif;
}




.buttonContainer {
  display: flex;
  justify-content: center;
  width: 250px;
  padding: 20px 0;
}

.buttonContainer .buttonContainer__button {
  height: 52px;
  width: 220px;
  background-image: linear-gradient(180deg, #bb83fc 0%, #5f79f6 100%);
  border: none;
  border-radius: 5px;
  box-shadow: 0 8px 10px 0 rgba(144, 127, 249, 0.39);
  transition: .3s ease;
  cursor: pointer;
}

.buttonContainer .buttonContainer__button:active {
  box-shadow: 0 0px 10px 0 rgba(144, 127, 249, 0.39);
}

.buttonContainer .buttonContainer__button:focus {
  outline: none;
}

.buttonContainer .buttonContainer__button .button__text {
  font-family: 'Ubuntu', sans-serif;
  font-size: 15px;
  font-weight: 900;
  color: #fff;
}
